/**
 * Admin module to manage applications OAuth 2
 * @author Alex Esteban Rojas Gutierrez
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.oauth2Applications", {}, {

    getWidgetName: function () {
        return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_OAUTH2APPLICATIONS;
    },

    init: function (workportalFacade, dataService, params) {
        var self = this;
        var templateName = "bizagi.workportal.desktop.widgets.admin.oauth2.applications";

        // Call base
        self._super(workportalFacade, dataService, params);

        // Load templates
        self.loadTemplates({
            "admin.oauth2applications": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-oauth2applications"),
            "admin.edit.oauth2applications": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-edit-oauth2applications"),
            "admin.add.oauth2applications": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-add-oauth2applications"),
            useNewEngine: false
        });

        // Model's declaration
        self.CurrentClientData = {};
    },

    renderContent: function () {
        var self = this;
        self.content = $.tmpl(self.getTemplate("admin.oauth2applications"));
        return self.content;
    },
    /* Load all the templates */
    loadtemplates: function () {
        var self = this;
        self.contentOauth = $.tmpl(self.getTemplate("admin.oauth2applications"));

    },

    postRender: function () {
        var self = this;
        var content = self.getContent();
        content.html(self.contentOauth);
        self.performSimpleGrid();
    },

    performSimpleGrid: function () {
        var self = this;
        var simpleGridPlugginOptions = {
            allowAdd: true,
            allowEdit: true,
            allowDelete: true,
            allow: true,
            orderEnabled: false,
            displayName: 'Oauth',
            pageSize: 10,
            onDataBinding: function (args) {
                var def = $.Deferred();
                $.when(self.dataService.getOAuth2Applications().done(function (data, args) {
                    var headers = ['Id', 'Name', 'Description', 'ClientId', 'ClientSecret', 'AuthorizationGrantType', 'AccessTokenLifetimeInMinutes','AllowedScope','DefaultUserDomain', 'DefaultUserName', 'RedirectUri', 'RedirectUriStrategy', 'Website'];
                    data.data.map(function (element) {
                        headers.forEach(function (hv) {
                            if (element[hv] === undefined) {
                                element[hv] = '';
                            }

                            if(hv === 'AuthorizationGrantType' && element[hv] === 'any'){
                                element[hv] = 'all';
                            }
                        });
                        return element;
                    });
                    var responseOauth = {};

                    if (data.data.length) {
                        responseOauth.header = $.map($(headers), function (item) {
                            return {
                                attributeType: 1004,
                                dataType: 2,
                                displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-applications-' + item.toLowerCase()),
                                fieldValue: 'idGuestSH',
                                xpath: 'GuestSH'
                            }
                        });
                    }

                    self.namesApplications = [];

                    responseOauth.row = $.map(data.data, function (item) {
                        self.namesApplications.push(item.Name);
                        return [$.map($(headers).filter(Object.keys(data.data[0])), function (key) {
                            var value = item[key]
                            if(key == "AllowedScope"){
                                value = (item[key][0]+' '+item[key][1]).replace("openid", "").trim();
                            }
                                
                            return value;
                        })];
                    });

                    /*responseOauth.total = data.data.length;
                     responseOauth.page = args.pag || 1;
                     responseOauth.records = data.data.length;*/

                    def.resolve(responseOauth);
                }).fail(function (error) {
                    bizagi.log(error);
                }));

                $.when(self.dataService.getCurrentUser()).done(function (currentUserData, args) {
                    self.userFullName = currentUserData.user;
                }).fail(function () { });

                return def.promise();
            },
            onAdd: function (e, args) {
                self.addApplication(this, 'New');
            },
            onEdit: function (e, args) {
                self.editApplication($(args).attr("data-idrowentity"));
            },
            onDelete: function (e, args) {
                self.deleteApplication($(args).attr("data-idrowentity"));
            },
            getResource: function (key) {
                /*self.getResource(key);*/
            }
        };

        $("#admin-oauth2-applications-table", self.getContent()).simpleGrid(self, simpleGridPlugginOptions).render();
    },

    addApplication: function (element, action) {
        var self = this;

        var tmplGT = $.tmpl(self.getTemplate("admin.add.oauth2applications"), {});
        tmplGT.appendTo("#admin-oauth2-add-application");

        $('#admin-oauth2-applications-wrapper').hide();
        $('#admin-oauth2-add-application').show();

        self.setupAddApplicationEvents();

        self.setupCombos("add");

    },

    setupAddApplicationEvents: function () {
        var self = this;
        $('#btn-admin-oauth2-add').click(function () { self.save() });
        $('#btn-admin-oauth2-cancel').click(function () { self.cancel("add") });
        self.setupUserSuggest();
    },

    setupUserSuggest: function (edit) {
        var type = '';
        if (edit) {
            type = 'edit-';
        }
        var self = this;
        var stp = null;
        var inputDomain = $('#biz-wp-oauth2-' + type + 'user-domain-input');
        var inputUserName = $('#biz-wp-oauth2-default-' + type + 'user-name-input');
        var options = $('#biz-wp-oauth2-default-' + type + 'user-name-options');
        $(inputUserName).focus(function (event) {
            if($(this).length > 0 && options[0].children.length > 0){
                $(options).css('display', 'block');
            }
            else{
                $(options).css('display', 'none');
            }
        });
        $(inputUserName).keyup(function (event) {
            var that = this;
            var up = 38;
            var down = 40;
            var select = 13;
            var proximo = 0;

            switch (event.keyCode) {
                case select:
                    selectData();
                    break;
                case up:
                    upOrDown("up");
                    break;
                case down:
                    upOrDown("down");
                    break;
                default:
                    searchNames();
                    break;
            }
            function selectData() {
                var target = $('#biz-wp-oauth2-default-' + type + 'user-name-options .selected:first').attr("data-target0");
                var domain = $('#biz-wp-oauth2-default-' + type + 'user-name-options .selected:first').attr("data-target1");
                $(that).val(target);
                $(inputDomain).val(domain)
                $(options).html('');
                $(options).css('display', 'none');
            }

            function upOrDown(direction) {
                $.each($('.user-option'), function (index) {
                    if ($(this).hasClass("selected")) {
                        switch (direction) {
                            case "down":
                                proximo += index + 1;
                                break;
                            case "up":
                                proximo = index - 1;
                                break;
                        }
                    }

                });
                if (proximo == $('.user-option').length) {
                    return false;
                } else if (proximo < 0) {
                    return false;
                }
                $.each($('.user-option'), function (index) {
                    $(this).removeClass('selected');
                    if (index === proximo) {
                        $(this).addClass('selected');
                    }
                })
            }
            function searchNames() {
                $(inputDomain).val('');
                $(options).fadeIn();
                if (stp !== null) { clearTimeout(stp); }
                stp = setTimeout(function (value) {
                    if (value != '') {
                        var params = { userName: value };
                        self.dataService.searchUsers(params)
                            .done(function (result) {
                                if (result.users && Array.isArray(result.users) && result.users.length > 0) {
                                    self.createOptionsArray(result.users.filter(function (x, i) { return (i < 5); }), options, [{ element: inputUserName, value: 'userName' }, { element: inputDomain, value: 'domain' }]);
                                } else {
                                    $(options).html('<div class="user-no-option">' + bizagi.localization.getResource('workportal-widget-admin-oauth2-user-search-no-results') + ' </div>');
                                }
                            })
                            .fail(function () { $(options).html(''); });
                    } else {
                        $(options).css('display', 'none');
                    }
                }, 400, event.currentTarget.value);
            }
        }).closest('.biz-wp-form-cell').focusout(function () {
            $(options).fadeOut();
        });

        // Access token input
        self.validationAccessTokenInput(edit ? "#biz-wp-oauth2-edit-access-token-input" : "#biz-wp-oauth2-access-token-input", edit)();
    },

    validationAccessTokenInput: function (accessTokenSelector, isEdit) {
        var accessTokenInput = $(accessTokenSelector);

        if(!isEdit) {
            accessTokenInput.val(0);
        }

        function updateSuggest() {
            var num = +$(accessTokenSelector).val();
            if(num === 0) {
                $("#suggest_lifetime").text(bizagi.localization.getResource('workportal-widget-admin-oauth2-applications-access-token-lifetime-never-expires'));
            } else {
                $("#suggest_lifetime").text("");
            }
        }

        accessTokenInput.change(updateSuggest);

        return updateSuggest;
    },

    createOptionsArray: function (users, options, targets) {
        var self = this;
        $(options).html('');
        users.forEach(function (user) {
            $(options).append(self.createOption(user, targets, options));
        });
    },
    createOption: function (value, targets, options) {
        var data = targets.reduce(function (result, target, index) { return result + 'data-target' + index + '="' + value[target.value] + '" ' }, '');
        return $('<div class="user-option" ' + data + "></div>")
            .click({ targets: targets }, function (ev) {
                var that = this;
                ev.data.targets.forEach(function (target, index) {
                    $(target.element).val($(that).data('target' + index));
                });
                $(options).html('');
            })
            .html(value.domain + ' \\ ' + value.userName);
    },

    setupCombos: function (type) {
        var self = this;
        var dataGrandType = [{
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-authorization-code'), //"Authorization Code",
            id: "authorization_code"
        },
        {
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-client-credentials'), //"Client credentials",
            id: "client_credentials"
        },
        {
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-all'), //All
            id: "any"
        },
        {
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-bearer-token'), // Bearer token
            id: "bearer_token"
        }
        ];

        var dataRedirectUri = [{
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-web-application'), //"Web Application",
            id: "WebApplication"
        },
        {
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-mobile-bar'), //"Mobile Title Bar Browser",
            id: "MobileTitleBarBrowser"
        },
        {
            displayName: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-mobile-port'), //"Mobile Localhost Port",
            id: "MobileLocalhostPort"
        }
        ];

        if (type === "add") {
            var $comboGrandType = $("#biz-wp-oauth2-grant-type-input");
            var $comboAllowedScope = $("#biz-wp-oauth2-allowed-scope-input");
            var $comboRedirectUri = $("#biz-wp-oauth2-redirect-uri-strategy-input");

            self.setupCombo($comboGrandType, dataGrandType, "grantType");
            self.setupCombo($comboRedirectUri, dataRedirectUri, "RedirectUri");

            self.initializeOauth2Template("authorization_code");
        }

        if (type === "edit") {
            var $comboEditGrandType = $("#biz-wp-oauth2-edit-grant-type-input");
            var $comboEditAllowedScope = $("#biz-wp-oauth2-edit-allowed-scope-input");
            var $comboEditRedirectUri = $("#biz-wp-oauth2-edit-redirect-uri-strategy-input");
            self.setupCombo($comboEditGrandType, dataGrandType, "editGrantType");
            self.setupCombo($comboEditRedirectUri, dataRedirectUri, "editRedirectUri");
        }
    },

    initializeOauth2Template: function(grantType) {
        var scopeLogin = $("#biz-wp-oauth2-edit-scope-login-input, #biz-wp-oauth2-scope-login-input");
        var scopeUserSync = $("#biz-wp-oauth2-scope-user-sync-input, #biz-wp-oauth2-edit-scope-user-sync-input");
        var scopeODataQuery = $("#biz-wp-oauth2-scope-odata-query-input, #biz-wp-oauth2-edit-scope-odata-query-input");
        var scopeRpa = $("#biz-wp-oauth2-scope-rpa-input, #biz-wp-oauth2-edit-scope-rpa-input");

        if(grantType === "bearer_token") {
            $(".bz-oauth2-only-authorization-code, .bz-oauth2-only-edit-authorization-code, #bz-oauth2-only-WebApplication, #bz-oauth2-edit-only-WebApplication").hide();
            $(".bz-oauth2-only-client-credentials, .bz-oauth2-only-edit-client-credentials").show();
            scopeUserSync.removeAttr("disabled").prop("checked", false);
            scopeLogin.attr("disabled", true).prop("checked", false);
            scopeRpa.attr("disabled", true).prop("checked", false);
        } else if (grantType === "client_credentials") {
            scopeLogin.attr("disabled", true).prop("checked", false);            
        } else if(grantType === "authorization_code") {
            scopeUserSync.attr("disabled", true).prop("checked", false);
            scopeODataQuery.attr("disabled", true).prop("checked", false);
            scopeRpa.attr("disabled", true).prop("checked", false);
        }
    },

    setupCombo: function (elem, data, dataSelf) {
        var self = this;

        if (dataSelf === "grantType") { self.grantType = data[0].id }
        if (dataSelf === "RedirectUri") { self.RedirectUri = data[0].id }

        self.pos = 0;

        if (dataSelf === "editGrantType") {
            self.editGrantType = self.editGrantType || data[0].id;
            $.each(data, function (i) {
                if (data[i].id === self.editGrantType) { self.pos = i; }
            })
        }

        if (dataSelf === "editRedirectUri") {
            self.editRedirectUri = self.editRedirectUri || data[0].id;
            $.each(data, function (i) {
                if (data[i].id === self.editRedirectUri) { self.pos = i; }
            })
        }

        if (self.comboElements) {
            self.comboElements.push(elem);
        } else {
            self.comboElements = [];
        }

        elem.uicombo({
            data: { combo: data },
            initValue: data[self.pos],
            itemValue: function (item) {
                return item.id;
            },
            itemText: function (item) {
                return item.displayName;
            },
            onChange: function (obj) {
                var bizOAuth2UserSyncInput = $("#biz-wp-oauth2-scope-user-sync-input, #biz-wp-oauth2-edit-scope-user-sync-input");
                var bizOAuth2ODataQueryInput = $("#biz-wp-oauth2-scope-odata-query-input, #biz-wp-oauth2-edit-scope-odata-query-input");
                var bizOAuth2RpaInput = $("#biz-wp-oauth2-scope-rpa-input, #biz-wp-oauth2-edit-scope-rpa-input");
                $(".bz-oauth2-only-authorization-code, .bz-oauth2-only-edit-authorization-code, #bz-oauth2-only-WebApplication, #bz-oauth2-edit-only-WebApplication").show();
                $("#biz-wp-oauth2-edit-scope-login-input, #biz-wp-oauth2-scope-login-input").removeAttr("disabled").prop("checked", false);

                if (dataSelf === "grantType" || dataSelf === "editGrantType") {
                    if (dataSelf === "grantType") { self.grantType = obj.ui.data("value") }
                    if (dataSelf === "editGrantType") { self.editGrantType = obj.ui.data("value") }
                    if (obj.ui.data("value") === "client_credentials") {
                        $(".bz-oauth2-only-edit-authorization-code").hide();
                        $(".bz-oauth2-only-authorization-code").hide();
                        $(".bz-oauth2-only-edit-client-credentials").show();
                        $(".bz-oauth2-only-client-credentials").show();
                        bizOAuth2UserSyncInput.removeAttr("disabled").prop("checked", false);
                        bizOAuth2ODataQueryInput.removeAttr("disabled").prop("checked", false);
                        bizOAuth2RpaInput.removeAttr("disabled").prop("checked", false);
                    } else if (obj.ui.data("value") === "authorization_code") {
                        $(".bz-oauth2-only-client-credentials").hide();
                        $(".bz-oauth2-only-authorization-code").show();
                        $(".bz-oauth2-only-edit-client-credentials").hide();
                        $(".bz-oauth2-only-edit-authorization-code").show();
                        bizOAuth2UserSyncInput.attr("disabled", true).prop("checked", false);
                        bizOAuth2ODataQueryInput.attr("disabled", true).prop("checked", false);
                        bizOAuth2RpaInput.attr("disabled", true).prop("checked", false);
                    } else if (obj.ui.data("value") === "bearer_token") { /*  */ } else {
                        $(".bz-oauth2-only-client-credentials").show();
                        $(".bz-oauth2-only-authorization-code").show();
                        $(".bz-oauth2-only-edit-client-credentials").show();
                        $(".bz-oauth2-only-edit-authorization-code").show();
                        bizOAuth2UserSyncInput.attr("disabled", true).prop("checked", false);
                        bizOAuth2ODataQueryInput.attr("disabled", true).prop("checked", false);
                        bizOAuth2RpaInput.attr("disabled", true).prop("checked", false);
                    }
                    self.initializeOauth2Template(obj.ui.data("value"));
                }

                if (dataSelf === "RedirectUri" || dataSelf === "editRedirectUri") {
                    if (dataSelf === "RedirectUri") { self.RedirectUri = obj.ui.data("value") }
                    if (dataSelf === "editRedirectUri") { self.editRedirectUri = obj.ui.data("value") }
                    if (obj.ui.data("value") === "WebApplication") {
                        $("#bz-oauth2-only-MobileLocalhostPort").hide();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort").hide();
                        $("#bz-oauth2-only-WebApplication").show();
                        $("#bz-oauth2-edit-only-WebApplication").show();
                    } else if (obj.ui.data("value") === "MobileTitleBarBrowser") {
                        $("#bz-oauth2-only-WebApplication").hide();
                        $("#bz-oauth2-only-MobileLocalhostPort").hide();
                        $("#bz-oauth2-edit-only-WebApplication").hide();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort").hide();
                    } else {
                        $("#bz-oauth2-only-WebApplication").hide();
                        $("#bz-oauth2-edit-only-WebApplication").hide();
                        $("#bz-oauth2-only-MobileLocalhostPort").show();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort").show();
                    }
                }

                if (dataSelf === "AllowedScope") { self.AllowedScope = obj.ui.data("value") }
                if (dataSelf === "editAllowedScope") { self.editAllowedScope = obj.ui.data("value") }
            }
        });
    },

    showErrors: function (target, messageToken) {
        target.show();
        target.html(bizagi.localization.getResource(messageToken));
    },

    dataValidating: function (form) {
        var self = this;
        var prefix = '';
        if (form === 'edit') {
            prefix = 'edit-';
        }

        var errorsBoard = $("#bz-oauth2-applications-errors-board");
        errorsBoard.hide();
        $("#bz-oauth2-applications-" + prefix + "error-scope-required").hide();

        // Validating App Name
        var inputName = $("#biz-wp-oauth2-" + prefix + "name-input").val();
        inputName = inputName.trim();

        if (inputName.length < 1) {
            self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-error-app-name-required');
            return false;
        } else if (self.namesApplications.findIndex(function (n) { return n === inputName; }) >= 0) {
            if (form !== 'edit' || self.CurrentClientData.Name !== inputName) {
                self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-error-name-duplicated');
                return false;
            }
        }

        // Validating Allowed scope
        if (!($("#biz-wp-oauth2-" + prefix + "scope-api-input").is(':checked')
            || $("#biz-wp-oauth2-" + prefix + "scope-login-input").is(':checked')
            || $("#biz-wp-oauth2-" + prefix + "scope-user-sync-input").is(':checked'))
        ) {
            self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-error-atleastone-allowed-scope');
            $("#bz-oauth2-applications-" + prefix + "error-scope-required").show();
            return false;
        }

        // Validating Data depending of GrantType
        //-- Client Credentials required
        var grantType = (form === 'save') ? self.grantType : self.editGrantType;

        var inputDefaultUserDomain = $("#biz-wp-oauth2-" + prefix + "user-domain-input").val().trim();
        var inputDefaultUserName = $("#biz-wp-oauth2-default-" + prefix + "user-name-input").val().trim();

        if (grantType === "client_credentials" || grantType === "any" || grantType === "bearer_token") {
            if (inputDefaultUserDomain.length < 1 || inputDefaultUserName.length < 1) {
                self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-user-domain-required');
                return false;
            }
        }

        //-- Auth Code required
        var RedirectUri = (form === 'save') ? self.RedirectUri : self.editRedirectUri;
        var inputRedirectUri = $("#biz-wp-oauth2-" + prefix + "redirect-uri-input").val().trim();
        var inputMobileHostPort = $("#biz-wp-oauth2-" + prefix + "mobile-host-port").val().trim();
        if (grantType === "authorization_code" || grantType === "any") {
            if (RedirectUri === "WebApplication") {
                if (inputRedirectUri.length < 1 || !(/^(ftp|http|https):\/\/[^ "]+$/.test(inputRedirectUri))) {
                    self.showErrors(errorsBoard, 'workportal-widget-admin-users-request-url-invalid');
                    return false;
                }
            } else if (RedirectUri === "MobileLocalhostPort") {
                if (inputMobileHostPort.length < 1 || isNaN(inputMobileHostPort) || (Math.floor(inputMobileHostPort) !== parseFloat(inputMobileHostPort))) {
                    self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-error-port-invalid');
                    return false;
                }
            }
        }

        // Validating Token Life Time
        var inputAccessTokenLifetimeInMinutes = $("#biz-wp-oauth2-" + prefix + "access-token-input").val().trim();
        if (inputAccessTokenLifetimeInMinutes.length < 1 || isNaN(inputAccessTokenLifetimeInMinutes) || (Math.floor(inputAccessTokenLifetimeInMinutes) !== parseFloat(inputAccessTokenLifetimeInMinutes))) {
            self.showErrors(errorsBoard, 'workportal-widget-admin-oauth2-error-time-invalid');
            return false;
        }

        return true;
    },

    save: function () {
        var self = this;

        if (self.dataValidating('save') === true) {
            var allowedScope = "";

            if ($("#biz-wp-oauth2-scope-api-input").is(':checked')) {
                allowedScope += "api";
            }

            if ($("#biz-wp-oauth2-scope-login-input").is(':checked')) {
                allowedScope += " login";
            }

            if ($("#biz-wp-oauth2-scope-user-sync-input").is(':checked')) {
                allowedScope += " usersync";
            }

            if ($("#biz-wp-oauth2-scope-odata-query-input").is(':checked')) {
                allowedScope += " odataquery";
            }

            if ($("#biz-wp-oauth2-scope-rpa-input").is(':checked')) {
                allowedScope += " rpa";
            }

            var dataAddApplication = {};
            dataAddApplication["authorization_grant_type"] = self.grantType;
            dataAddApplication["allowed_scope"] = allowedScope;
            dataAddApplication["username_creator"] = self.userFullName;
            dataAddApplication["redirect_uri_strategy"] = self.RedirectUri;

            /*This value is necesary because service returns error: url is malformed*/
            if (self.grantType === "client_credentials" || self.grantType === "bearer_token") {
                dataAddApplication["redirect_uri"] = "http://localhost--";
            }

            var dataInputs = $("#formAddOAuthApp :input").serializeArray();
            for (var i = 0; i < dataInputs.length; i++) {
                if (dataInputs[i].value) {
                    if (dataInputs[i].name === "redirect_uri_port" && self.RedirectUri === "MobileLocalhostPort") {
                        dataAddApplication["redirect_uri"] = dataInputs[i].value;
                    } else {
                        dataAddApplication[dataInputs[i].name] = dataInputs[i].value;
                    }
                }
            }

            self.dataService.createOAuth2Application(dataAddApplication).done(function () {
                self.performSimpleGrid();
                self.cancel("add");
            });
        }
    },

    getGrantType: function (index) {
        var dataGrandType = {
            authorization_code: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-authorization-code'), //"Authorization Code",
            client_credentials: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-client-credentials'), //"Client credentials",
            any: bizagi.localization.getResource('workportal-widget-admin-oauth2-grandtype-all'), //"All",
        };
        return dataGrandType[index];
    },

    getRedirectStrategy: function (index) {
        var dataRedirectStrategy = {
            WebApplication: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-web-application'), //"Web Application",
            MobileTitleBarBrowser: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-mobile-bar'), //"Mobile Title Bar Browser",
            MobileLocalhostPort: bizagi.localization.getResource('workportal-widget-admin-oauth2-redirecturi-mobile-port'), //"Mobile Localhost Port",
        };
        return dataRedirectStrategy[index];
    },
    getMobileLocalhostPort: function (uri, strategy) {
        if (strategy === "MobileLocalhostPort") {
            var port = uri.match(/:([0-9]+)/)[1];
            return parseInt(port);
        } else {
            return '';
        }
    },

    //LOAD FORM TO EDIT OAUTH CLIENT
    editApplication: function (applicationId) {
        var self = this;
        var content = self.getContent();
        content = (content[4]) ? content[4] : content[2];

        $.when(self.dataService.getOAuth2Application(applicationId)).done(function (data) {
            if (data.success) {
                // data = data.data;
                self.CurrentClientData = data.data;
                if(self.CurrentClientData.RedirectUriStrategy != "WebApplication")
                    self.CurrentClientData.uri = '';
                else
                    self.CurrentClientData.uri = self.CurrentClientData.RedirectUri;
                var tmplGT = $.tmpl(
                    self.getTemplate("admin.edit.oauth2applications"), {
                        editGrantType: self.CurrentClientData.AuthorizationGrantType,
                        data: self.CurrentClientData
                    }, {
                        getGrantType: self.getGrantType,
                        getRedirectStrategy: self.getRedirectStrategy,
                        getMobileLocalhostPort: self.getMobileLocalhostPort
                    }
                );
                tmplGT.appendTo(content);

                self.ActualNameForEdit = self.CurrentClientData.Name;
                self.DataAllowedScope = self.CurrentClientData.AllowedScope;

                self.editGrantType = self.CurrentClientData.AuthorizationGrantType;
                self.editRedirectUri = self.CurrentClientData.RedirectUriStrategy;

                function checkedScope(scope) {
                    return $.grep(self.CurrentClientData.AllowedScope, function (n) { return n === scope }).length;
                }

                if (checkedScope("api") > 0) {
                    $("#biz-wp-oauth2-edit-scope-api-input", content).prop("checked", true);
                }

                if (checkedScope("login") > 0) {
                    $("#biz-wp-oauth2-edit-scope-login-input", content).prop("checked", true);
                }

                if (checkedScope("usersync") > 0) {
                    setTimeout(function () { // Se debe esperar que el elemento se renderice completamente
                        $("#biz-wp-oauth2-edit-scope-user-sync-input", content).prop("checked", true);
                    });
                }

                if (checkedScope("odataquery") > 0) {
                    setTimeout(function () { // Se debe esperar que el elemento se renderice completamente
                        $("#biz-wp-oauth2-edit-scope-odata-query-input", content).prop("checked", true);
                    });
                }

                if (checkedScope("rpa") > 0) {
                    setTimeout(function () { // Se debe esperar que el elemento se renderice completamente
                        $("#biz-wp-oauth2-edit-scope-rpa-input", content).prop("checked", true);
                    });
                }

                //Visual Setup for Grant Type, Allowed Scope and Redirect Strategy
                if (!self.CurrentClientData.IsNative) {
                    self.setupCombos("edit");

                    if (self.editGrantType === "client_credentials") {
                        $(".bz-oauth2-only-edit-client-credentials", content).show();
                        $(".bz-oauth2-only-edit-authorization-code", content).hide();
                    } else if (self.editGrantType === "authorization_code") {
                        $(".bz-oauth2-only-edit-client-credentials", content).hide();
                        $(".bz-oauth2-only-edit-authorization-code", content).show();
                    } else if (self.editGrantType === "bearer_token") {
                        //
                    } else {
                        $(".bz-oauth2-only-edit-client-credentials", content).show();
                        $(".bz-oauth2-only-edit-authorization-code", content).show();
                    }

                    self.initializeOauth2Template(self.editGrantType);

                    if (self.CurrentClientData.RedirectUriStrategy === "WebApplication") {
                        $("#bz-oauth2-edit-only-WebApplication", content).show();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort", content).hide();
                    } else if (self.CurrentClientData.RedirectUriStrategy === "MobileTitleBarBrowser") {
                        $("#bz-oauth2-edit-only-WebApplication", content).hide();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort", content).hide();
                    } else {
                        $("#bz-oauth2-edit-only-WebApplication", content).hide();
                        $("#bz-oauth2-edit-only-MobileLocalhostPort", content).show();
                    }
                } else {
                    //Disable Allowed Scope Checkbox
                    $("#biz-wp-oauth2-edit-scope-api-input", content).attr("disabled", true);
                    $("#biz-wp-oauth2-edit-scope-login-input", content).attr("disabled", true);
                }
            }

            $('#admin-oauth2-applications-wrapper').hide();
            $(content).show();

            //Buttons Setup
            $('#btn-admin-oauth2-update-keys', content).click(function () { self.updateApplicationKeys() });
            $('#btn-admin-edit-oauth2-cancel', content).click(function () { self.cancel("edit") });
            if (!self.CurrentClientData.IsNative) {
                $('#btn-admin-edit-oauth2-add', content).click(function () { self.updateApplication() });
            } else {
                $('#btn-admin-edit-oauth2-add', content).click(function () { self.updateNaviteApplication() });
            }
            self.setupUserSuggest(true);

        }).fail(function () { });
    },

    //UPDATE ACTION FOR NATIVE APPLICATIONS
    updateNaviteApplication: function () {
        var self = this;
        var dataAddApplication = { accessTokenLifetimeInMinutes: "" };
        dataAddApplication.accessTokenLifetimeInMinutes = parseInt($("#biz-wp-oauth2-edit-access-token-input").val(), 10);
        if (!isNaN(dataAddApplication.accessTokenLifetimeInMinutes) && dataAddApplication.accessTokenLifetimeInMinutes > 0) {
            self.dataService.updateOAuth2Application(dataAddApplication, self.CurrentClientData.Id).done(function () {
                self.performSimpleGrid();
                self.cancel("edit");
            });
        } else {
            $("#bz-oauth2-applications-error-time-invalid").show();
        }
    },

    //UPDATE ACTION
    updateApplication: function () {
        var self = this;
        if (self.dataValidating('edit') === true) {
            var appId = $("#biz-wp-oauth2-edit-id-input").val();
            var AllowedScope = "";

            if ($("#biz-wp-oauth2-edit-scope-api-input").is(':checked')) {
                AllowedScope += "api";
            }

            if ($("#biz-wp-oauth2-edit-scope-login-input").is(':checked')) {
                AllowedScope += " login";
            }

            if ($("#biz-wp-oauth2-edit-scope-user-sync-input").is(':checked')) {
                AllowedScope += " usersync";
            }

            if ($("#biz-wp-oauth2-edit-scope-odata-query-input").is(':checked')) {
                AllowedScope += " odataquery";
            }

            if ($("#biz-wp-oauth2-edit-scope-rpa-input").is(':checked')) {
                AllowedScope += " rpa";
            }

            var dataAddApplication = {};
            dataAddApplication["authorization_grant_type"] = self.editGrantType;
            dataAddApplication["allowed_scope"] = AllowedScope;
            dataAddApplication["username_creator"] = self.userFullName;
            dataAddApplication["redirect_uri_strategy"] = self.editRedirectUri;

            if (self.editGrantType === "client_credentials") {
                dataAddApplication["redirect_uri"] = "http://localhost--";
            }

            var dataInputs = $("#formEditOAuthApp :input").serializeArray();
            for (var i = 0; i < dataInputs.length; i++) {
                if (dataInputs[i].value) {
                    if (dataInputs[i].name === "redirect_uri_port" && self.editRedirectUri === "MobileLocalhostPort") {
                        dataAddApplication["redirect_uri"] = "http://localhost:" + dataInputs[i].value;
                    } else {
                        dataAddApplication[dataInputs[i].name] = dataInputs[i].value;
                    }

                }
            }

            self.dataService.updateOAuth2Application(dataAddApplication, appId).done(function (resp) {
                if(resp && resp.error === "user_not_found") {
                    bizagi.showMessageBox(bizagi.localization.getMessageFromLocalization("workportal-general-error-1150", 'none', [dataAddApplication['default_username']]),
                        null,
                        'warning'
                    );
                } else {
                    self.performSimpleGrid();
                    self.cancel("edit");
                }
            }).fail(function (jqXHR, textStatus, errorThrown) {
                console.warn(arguments)
            });
        }
    },

    //UPDATE OAUTH CLIENT KEYS ACTION
    updateApplicationKeys: function () {
        var self = this;
        $.when(bizagi.showConfirmationBox(bizagi.localization.getResource("workportal-widget-admin-oauth2-applications-update-keys-confirmation"), "Bizagi", "warning")).done(function () {
            var appId = $("#biz-wp-oauth2-edit-id-input").val();
            var params = {
                client_id: self.CurrentClientData.ClientId,
                client_secret: self.CurrentClientData.ClientSecret
            };
            self.dataService.updateClientSecretKeysOAuth2Application(params, appId).done(function (data) {
                if (data.success) {
                    self.performSimpleGrid();
                    self.cancel("edit");
                } else {
                    $.when(bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-oauth2-error-update-keys"), null, 'warning')).done(function () {
                        self.performSimpleGrid();
                        self.cancel("edit");
                    });
                }
            }).fail(function (err) {
                $.when(bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-oauth2-error-update-keys"), null, 'warning')).done(function () {
                    self.performSimpleGrid();
                    self.cancel("edit");
                });
            });
        });
    },

    //DELETE ACTION
    deleteApplication: function (applicationId) {
        var self = this;
        $.when(bizagi.showConfirmationBox(bizagi.localization.getResource("render-grid-delete-confirmation"), "Bizagi", "warning")).done(function () {
            self.dataService.deleteOAuth2Application(applicationId).done(function (data) {
                self.performSimpleGrid();
            })
        });
    },

    //CANCEL ACTION
    cancel: function (type) {
        var self = this;
        if (type === "add") { $('#admin-oauth2-add-application').children().remove() }
        if (type === "edit") { $('#admin-oauth2-edit-application').children().remove() }
        $('#admin-oauth2-applications-wrapper').show();
        self.cleanData();
    },

    //CLEAN DATA
    cleanData: function () {
        self.AllowedScope = null;
        self.editAllowedScope = null;
        self.userFullName = null;
        self.grantType = null;
        self.editGrantType = null;
        self.RedirectUri = null;
        self.editRedirectUri = null;
        $(".biz-wp-input-text").val("");
        $(".biz-wp-oauth2-select-control").children().remove();
        self.CurrentClientData = {};
    },
    destroy: function () {

    }

});
