/**
 * Admin module to manage entities
 * @author Andres Felipe Arenas Vargas, Ricardo Alirio Perez Diaz
 */
bizagi.workportal.widgets.admin.entities.extend("bizagi.workportal.widgets.admin.entities", {}, {
    /**
     * Init constructor
     * @param workportalFacade The workportalFacade to access to all components
     * @param dataService The dataService for making requests
     * @param params Widget Description
     */
    init: function (workportalFacade, dataService, params) {
        var self = this;
        var templateName = "bizagi.workportal.desktop.widgets.admin.entities";
        self.entitiesState = [];
        self.actualShowingList = false;
        // Call base
        self._super(workportalFacade, dataService, params);
        self._referrer = "entity";
        //Load templates
        self.loadTemplates({
            "admin.entity.wrapper": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-entity-wrapper"),
            "admin.entity.panel.wrapper": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-entity-panel-wrapper"),
            "admin.entity.panel.wrapper-buttons": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-entity-panel-wrapper-buttons"),
            "admin.entity.list": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-entity-list"),
            "admin.entity.error": bizagi.getTemplate(templateName).concat("#ui-bizagi-workportal-widget-admin-entity-error"),
            useNewEngine: false
        });

        // Store
        self._state = {
        };

        self.setInitialState();
    },
    /**
     * Load all the templates
     */
    loadtemplates: function () {
        var self = this;
        self.panelWrapper = $.tmpl(self.getTemplate("admin.entity.panel.wrapper"));
        self.panelWrapperButtons = self.getTemplate("admin.entity.panel.wrapper-buttons");
        self.entitiesList = self.getTemplate("admin.entity.list");
        self.entitiesError = self.getTemplate("admin.entity.error");
    },
    /**
     * Renders the content for the current widget
     * Get the list of all parametric entities and display them in the Template
     * If there is an error, display a a message
     */
    postRender: function () {
        var self = this;
        var content = self.getContent();
        var defer = $.Deferred();
        content.empty();
        $.when(self.dataService.getAdminEntitiesList())
            .done(function (data) {
                if (data.length > 0) {
                    self.entities = data;
                    var entityListWrapper = $("#admin-entity-list-wrapper", self.panelWrapper);

                    content.html(self.panelWrapper);
                    $.tmpl(self.entitiesList, { entitiesList: self.entities }).appendTo(entityListWrapper);
                    $("#entitiesList option:first", entityListWrapper).attr('selected', 'selected');

                    self.configureHandlers();
                }
                else {
                    var notFoundMessage = self.getResource("workportal-general-first-line-no-records-found");
                    content.html($.tmpl(self.entitiesError, { errorMessage: notFoundMessage }));
                }
                defer.resolve();
            })
            .fail(function (error) {
                self.entities = undefined;
                var errorMessage = self.getResource("workportal-widget-admin-entities-error");
                content.html($.tmpl(self.entitiesError, { errorMessage: errorMessage }));
                defer.reject();
            });
        return defer.promise();
    },
    /**
     * Configure handlers
     */
    configureHandlers: function () {
        var self = this;
        var entityListWrapper = $("#admin-entity-list-wrapper", self.panelWrapper);

        //Catch the typed text in the search field and filter the entities list
        $("#searchEntity", entityListWrapper).keyup(function () {
            var includeOpt = false;
            var matchString = $(this).val().toLowerCase();

            $.each(self.entities, function () {
                var el = $('#entitiesList option[value="' + this.guid + '"]', entityListWrapper)
                if (matchString.length > 1) {
                    if (this.displayName.toLowerCase().indexOf(matchString) != -1) {
                        if ($(el).parent('span.toggleOption').length) {
                            $(el).unwrap();
                        }
                    } else {
                        if ($(el).parent('span.toggleOption').length == 0) {
                            $(el).wrap('<span class="toggleOption" style="display: none;" />');
                        }
                    }
                } else {
                    if ($(el).parent('span.toggleOption').length) {
                        $(el).unwrap();
                    }
                }
            });
        });
        // EntitiesList listener
        $("#entitiesList", entityListWrapper).change(function () {
            $('#entitiesList>span>option:selected', self.panelWrapper).prop('selected', false);
           self.addButtons();
            self.setInitialState();
            self.reviewUploadFileState();
            self.drawSimpleGrid({ page: 1 });
        }).trigger("change");

        $("#bz-btn-language-settings", entityListWrapper).click(function () {
            self.openLanguageSettingsWidgetDialog();
        });
        
        $(".button-toggle-manage-entities", self.panelWrapper).click(function() {
            $(".buttons-wrapper", self.panelWrapper).toggle();
            self.resizeAdminEntityWrapper();
            $(".button-toggle-manage-entities", self.panelWrapper).toggleClass("collapse-arrow");
        });
    },

     /**
     * add buttons to upload excel files
     */
    addButtons: function () {
        var self = this;
        var wrapperButtons = $(".buttons-wrapper", self.panelWrapper);
        wrapperButtons.html($.tmpl(self.panelWrapperButtons));

        $("#downloadButton", self.panelWrapper).click(function () {
            self.dataService.getEntityExcel(self.getSelectedItem().guidEntity)
         });
        $("#admin-entity-main-wrapper .admin-entity-last-report-link").click(function () {
            self.downloadLastReport();
        });

        $("#file-upload", self.panelWrapper).change(function (file) {
            self.uploadFile(file);
        });

        $("#admin-entity-main-wrapper .admin-entity-last-report-link").click(function () {
            self.downloadLastReport();
        });
    },

    /**
     * Resize the height of the admin Entity warpper container according to the height to the butoon wrapper container
     */
    resizeAdminEntityWrapper: function(){
        var heightButtonWrapper = $('.manage-entities-container').height() + 12.83;
        if (!bizagi.util.isEnableFeature("enableUploadBatchEntities")) {
            heightButtonWrapper = 0;
            $('.manage-entities-container').hide();
        }
        $('#admin-entity-main-wrapper .admin-dialog-rightframe').css('height', 'calc(100% - '+heightButtonWrapper+'px)');
    },
     /**
     * toggle buttons wrapper
     */
    toggleButtonsWrapper: function () {
        var self = this;
        $(".buttons-wrapper", self.panelWrapper).toggle();
    },
     /**
     * upload an excel file
     */
    uploadFile: function () {
        var self = this;
        var guidEntity = self.getSelectedItem().guidEntity;
        var url = self.dataService.serviceLocator
            .getUrl("admin-entities-upload-excel")
            .replace("{EntityGuid}", guidEntity)
            .concat("?xsrf=" + bizagi.getXSRFToken());
        $("#file-upload").kendoUpload({
            async: {
                saveUrl: url,
                saveField: guidEntity
            },
            validation: {
                allowedExtensions: [".xlsx"],
                maxFileSize: bizagi.currentUser ? bizagi.currentUser.uploadMaxFileSize : 25000
            },
            showFileList: true,
            multiple: false,
            select: function (e) {
                self.panelWrapper.find(".k-widget").hide();
            },
            upload: function (e) {
                var isValid = validateFile(e);
                if (isValid === "true") {
                    self.entitiesState[guidEntity] = "UPLOADING";
                    self.reviewUploadFileState();
                } else {
                    self.showMessage(isValid, true);
                    e.preventDefault();
                }
            },
            error: function() {
                self.entitiesState[guidEntity] = "Error";
                self.reviewUploadFileState();
            },
            success: function (e) {
                guidEntity = e.sender.options.async.saveField;
                self.entitiesState[guidEntity] = "InProgress";
                self.reviewUploadFileState();
            }
        });
        function validateFile(e) {
            return $.map(e.files, function (file) {
                var errorMessage = "";
                if (file.extension !== e.sender.options.validation.allowedExtensions[0]) {
                    errorMessage = self.getResource("render-upload-error-extensions")+".";
                }
                else
                    if (file.size > e.sender.options.validation.maxFileSize) {
                        errorMessage = self.getResource("render-upload-iis-maxsize")+".";
                    }
                return errorMessage != "" ? errorMessage : true;
            }).join(", ");
        }
    },
     /**
	 * To open the Language Settings widget dialog
	 */
	openLanguageSettingsWidgetDialog: function() {
        var self = this;
		var defaultWidth = ($(window).width() - (($(window).width() * 15)/100)) + "px";
        var widgetParameters = {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_LANGUAGE,
            data: { "idCase": "LanguageAdmin" },
            closeVisible: false,
            maximize: true,
            modalParameters: {
                title: self.getResource("workportal-menu-submenu-LanguageAdmin"),
                width: defaultWidth,
                id: "LanguageAdmin"
            }
        };
        self.publish("showDialogWidget", widgetParameters);
	},
    /**
     * show a message 
     */
    showMessage: function (message, isError) {
        var self = this;
        var icon  = '';
        if(isError){
            icon = '<svg  class="bz-icon bz-icon-16 bz-icon-svg"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-clear-circle"></use></svg>';
            $("#upload-message", self.content).removeClass("success");
            $("#upload-message", self.content).addClass("error");
        }else{
            icon = '<svg  class="bz-icon bz-icon-16 bz-icon-svg"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#check-mark"></use></svg>';
            $("#upload-message", self.content).removeClass("error");
            $("#upload-message", self.content).addClass("success");
        }
        $("#upload-message", self.panelWrapper).html(icon + message).css("display", "block").fadeIn("slow", self.resizeAdminEntityWrapper);
        setTimeout(function() {
            $("#upload-message", self.panelWrapper).fadeOut("slow", self.resizeAdminEntityWrapper);
        }, 10000);
    },
     
    /**
     * Init the simple grid pluggin to display all the instances of the selected entity
     */
    drawSimpleGrid: function (params) {
        var self = this;
        self.actualShowingList = true;
        var selectedEntity = $.extend(self.getSelectedItem(), params);
        if (selectedEntity.idDisplayForm && selectedEntity.idDisplayForm != "00000000-0000-0000-0000-000000000000") {
            var formParams = {
                guid: selectedEntity.guidEntity,
                guidForm: selectedEntity.idDisplayForm,
                isStakeholder: false
            };
            $.when(self.dataService.getAdminEntitiesForm(formParams))
                .done(function (responseFormDisplay) {
                    self.performSimpleGrid(selectedEntity, responseFormDisplay);
                });
        }
        else {
            self.performSimpleGrid(selectedEntity);
        }
    },

    /**
     * Process entity form
     * @param content
     * @param responseEntity
     * @param responseFormDisplay
     */
    resolveEntityForm: function (responseEntity) {
        var self = this;
        var def = new $.Deferred();
        var newResponseEntity = {};
        var responseFormDisplay;

        /**
         * Get render elements within a form
         * @param elements
         * @return {Array}
         */
        var getRenderElements = function (elements) {
            var result = [];
            elements = elements || {};

            $.each(elements, function (key, value) {
                if (value.container) {
                    result.push.apply(result, getRenderElements(value.container.elements));
                } else {
                    result.push(value.render.properties);
                }
            });

            return result;
        };

        var headerIsAnEntity = function (header) {
            var entity = "";
            // Search xpath within responseEntity object and check if it is an entity
            $.each(responseEntity.header, function (key, value) {
                if (value.fieldValue == header.xpath && (value.entity || value.relatedEntity)) {
                    entity = value.entity || value.relatedEntity;
                }
            });

            return entity;
        };
        /**
         * Create headers from a form
         * @param elements
         * @param headers
         * @return {Array}
         */
        var getFormHeaders = function (elements, headers) {
            var result = [];
            elements = getRenderElements(elements || {});

            $.each(elements, function (key, value) {
                var attributeType = value.dataType;
                var entity = headerIsAnEntity(value);
                var headerProperties = {};
                switch (value.type) {
                    case "upload":
                        attributeType = 1002;
                        break;
                    case "image":
                        attributeType = 1003;
                        break;
                }

                headerProperties = $.extend(value, {
                    "guidAttribute": matchHeaderWithXpath(headers, value.xpath),
                    "fieldValue": value.xpath,
                    "attributeType": attributeType
                });

                if (entity) {
                    headerProperties.relatedEntity = entity;
                }

                result.push(headerProperties);
            });

            return result;
        };

        /**
         * find the header with the xpath for return the guidAttribute
         * @param {*} headers 
         * @param {*} xpath 
         */
        var matchHeaderWithXpath = function (headers, xpath) {
            var header = headers.filter(function (header) {
                return xpath == header.fieldValue;
            });
            return header.length > 0 ? header[0].guidAttribute : undefined;
        };

        /**
         * Get all values of a render controls
         * @param surrogatedKeys
         * @return {*}
         */
        var getFormDataRows = function (surrogatedKeys) {
            var def = new $.Deferred();
            var dataRows = [];
            var queueForms = [];
            var selectedEntity = self.getSelectedItem();
            var formParams = {
                guid: selectedEntity.guidEntity,
                guidForm: selectedEntity.idDisplayForm,
                isStakeholder: false
            };
            surrogatedKeys = surrogatedKeys || [];

            // If there are not surrogatedKeys, call  getAdminEntitiesForm service
            // without idRow, just for get headers
            if (surrogatedKeys.length == 0) {
                $.when(self.dataService.getAdminEntitiesForm(formParams)).done(function (result) {
                    if (!responseFormDisplay) {
                        responseFormDisplay = result;
                    }
                    // Resolve empty array of rows
                    def.resolve([]);
                });
            } else {
                $.each(surrogatedKeys, function (key, value) {
                    var params = bizagi.clone(formParams);
                    params.idRow = value;
                    queueForms.push(self.dataService.multiaction().getAdminEntitiesForm(params));
                });
                // Resolve Queue of deferreds
                $.when.apply($, queueForms).done(function () {
                    var responses = Array.prototype.slice.call(arguments);
                    $.map(responses, function (response, key) {
                        var responseForm = response;
                        if (!responseFormDisplay) {
                            responseFormDisplay = responseForm;
                        }
                        var elements = getRenderElements(responseForm.form.elements);
                        //Add surrogated key as a first element
                        var surrogatedKey = surrogatedKeys[key];
                        var values = [surrogatedKey];

                        $.each(elements, function (elmKey, elmValue) {
                            var value = elmValue.value;
                            switch (elmValue.type) {
                                case "image":
                                case "upload":
                                    value = "...";
                                    break;
                                case "boolean":
                                    value = (elmValue.value) ? "X" : "";
                                    break;
                                case "combo":
                                    if (typeof elmValue.value == "object" && elmValue.value.length > 0) {
                                        if (typeof elmValue.value[0].value == "object") {
                                            value = elmValue.value[0].value.join(",");
                                        } else {
                                            value = elmValue.value[0].value;
                                        }
                                    } else {
                                        value = ""
                                    }
                                    break;
                            }
                            values.push(value);
                        });

                        dataRows.push(values);

                        if (dataRows.length == surrogatedKeys.length) {
                            def.resolve(dataRows);
                        }
                    });
                });
            }
            return def.promise();
        };

        /**
         * Get all surrogated keys
         * @param entityObj
         * @return {Array}
         */
        var getSurrogatedKeys = function (entityObj) {
            var surrogatedKeys = [];
            entityObj = entityObj || {};
            if (entityObj.row) {
                $.each(entityObj.row, function (key, value) {
                    // Always the first element is a surrogated key
                    surrogatedKeys.push(value[0]);
                });
            }
            return surrogatedKeys;
        };

        var surrogatedKeys = getSurrogatedKeys(responseEntity);

        $.when(getFormDataRows(surrogatedKeys)).done(function (dataRows) {
            // Add surrogatedkey header
            var headers = [responseEntity.header[0]];
            headers.push.apply(headers, getFormHeaders(responseFormDisplay.form.elements, responseEntity.header));
            newResponseEntity = {
                header: headers,
                entity: responseEntity.entity,
                row: dataRows,
                total: responseEntity.total,
                page: responseEntity.page,
                records: responseEntity.records
            };
            // self.renderTableEntity(content, params, newResponseEntity);
            def.resolve(newResponseEntity);
        });
        return def.promise();
    },

    performSimpleGrid: function (selectedEntity, formDisplay) {
        var self = this;
        var simpleGridPlugginOptions = {
            allowAdd: selectedEntity.allowAdd,
            allowEdit: selectedEntity.allowEdit,
            displayName: selectedEntity.displayName,
            pageSize: 10,
            page: parseInt(selectedEntity.page) || 1,
            orderField: selectedEntity.orderField || "",
            orderType: selectedEntity.orderType || "asc",
            filters: selectedEntity.filters,
            onDataBinding: function (args) {
                args.pag = args.pag || self.getCurrentPageToSimpleGrid();
                var def = $.Deferred();
                if (args.pag) {
                    self.setSelectedItem('page', args.pag);
                }
                $.when(self.dataService.getAdminEntitiesRowData($.extend(args, { guidEntity: selectedEntity.guidEntity })))
                    .done(function (responseEntity) {
                        if (selectedEntity.guidEntity != self.getSelectedItem().guidEntity)
                            return;
                        if (formDisplay) {
                            responseEntity.relatedEntity = selectedEntity.guidEntity;
                            var oldResponse = self.reshapeEntity(responseEntity, formDisplay);
                            $.when(self.resolveEntityForm(responseEntity)).done(function (newResponseEntity) {
                                def.resolve(newResponseEntity);
                            });
                        } else {
                            def.resolve(responseEntity);
                        }
                    })
                    .fail(function (error) {
                        var errorMessage = self.getResource("workportal-widget-admin-entities-instances-error");
                        $("#admin-entity-instances-wrapper", self.getContent()).html($.tmpl(self.entitiesError, { errorMessage: errorMessage }));
                    });
                return def.promise();
            },
            onLoadFilter: function (args) {
                if (args.type === "Entity") {
                    return self.dataService.getAdminEntityData(args);
                }
            },
            onFilterChanged: function () {
                self.setInitialState();
            },
            onPageChanging: function (beforePage, newPage) {
                self.setCurrentPageToSimpleGrid(newPage);
            },
            onAdd: function (e, args) {
                self.validateVersionForm(this, 'New');
            },
            onEdit: function (e, args) {
                self.validateVersionForm(args, 'Edit');
            },
            getResource: function (key) {
                self.getResource(key);
            }
        };
        $("#admin-entity-instances-wrapper", self.panelWrapper).simpleGrid(self, simpleGridPlugginOptions).render();
    },
    /**
     * Return all the data of the selected item
     * @return {{guidEntity: *, displayName: *, allowAdd: boolean, allowEdit: boolean}}
     */
    getSelectedItem: function () {
        var self = this;
        var $selectedEntity = $('#entitiesList option:selected', self.panelWrapper);
        var params = {
            guidEntity: $selectedEntity.val(),
            displayName: $selectedEntity.text(),
            allowAdd: ($selectedEntity.attr("data-allowAdd").toLowerCase() === "true") ? true : false,
            allowEdit: ($selectedEntity.attr("data-allowEdit").toLowerCase() === "true") ? true : false,
            idDisplayForm: $selectedEntity.attr("data-idDisplayForm"),
            page: $selectedEntity.attr("data-page"),
            row: self.currentIdRow,
            orderField: $selectedEntity.data("orderField"),
            orderType: $selectedEntity.data("orderType"),
            filters: $selectedEntity.data("filters")
        };
        return params;
    },

    setSelectedItem: function (property, value) {
        var self = this;
        var $selectedEntity = $('#entitiesList option:selected', self.panelWrapper);
        $selectedEntity.attr('data-' + property, value);
    },

    /**
     * Build the correct data to get the form based on the action {Edit, New}
     * If the action is:
     * - New: {guidEntity, guidForm}
     * - Edit: {surrogateKey, guidEntity, guidForm}
     * @param element The selected element
     * @param action The action to perform
     */
    validateVersionForm: function (element, action) {
        var self = this;
        self.actualShowingList = false;
        var $selectedEntity = $('#entitiesList option:selected', self.panelWrapper);
        var entity = self.entities.find(function (e) { return e.guid === $selectedEntity.val() });
        var params = {
            guid: $selectedEntity.val()
        };
        if (action === 'New') {
            params.guidForm = (typeof entity.idAddForm !== "undefined") ? entity.idAddForm : "00000000-0000-0000-0000-000000000000";
        }
        else if (action == 'Edit') {
            params.idRow = $(element).attr('data-idrowentity');
            params.guidForm = (typeof entity.idEditForm !== "undefined") ? entity.idEditForm : "00000000-0000-0000-0000-000000000000";
        }

        //Empty the canvas to render the form
        $('#detailEntity', self.panelWrapper).empty();
        if (params.guidForm === "00000000-0000-0000-0000-000000000000") {
            self.getFormEntity(params);
        }
        else {
            $.when(self.dataService.getAdminEntityMigrated(params.guidForm)).done(function (data) {
                if (data.result == 0 || data.result == 10) {
                    self.getFormEntity(params);
                }
                else {
                    if (data.result == -9) {
                        var errorMessage = self.getResource("workportal-widget-admin-entities-instances-error");
                        $("#admin-entity-instances-wrapper", self.getContent()).html($.tmpl(self.entitiesError, { errorMessage: errorMessage }));
                    }
                    else {
                        if (action == 'New') {
                            params.idRow = -1;
                        }
                        var url = 'App/Admin/Entity.aspx?giudEntity=' + params.guid + '&idSurrogateKey=' + params.idRow + '&referer=entityadmin&Action=' + action + '&idForm=' + params.idForm;
                        self.loadIframeForm(url, 'otherForm');
                    }
                }
            });
        }
    },
    /**
     * Collect the data changed in the form and save the values
     * @param formRender the Form Rendered
     */
    saveFormEntity: function (formRender) {
        var defSave = $.Deferred();
        var serviceRender = new bizagi.render.services.service();
        var renderValues = {};

        formRender.collectRenderValues(renderValues);
        $.when(serviceRender.multiactionService.submitData({
            action: "SAVE",
            contexttype: "entity",
            data: renderValues,
            surrogatekey: (typeof formRender.surrogatekey !== "undefined") ? formRender.surrogatekey : false,
            idPageCache: formRender.idPageCache
        })).done(function (formRender) {
            if (formRender.type == "success") {
                defSave.resolve(true);
            }
            else {
                defSave.reject();
            }
        }).fail(function (formError) {
            bizagi.showMessageBox(formError.message);
            defSave.reject();
        });
        return defSave.promise();
    },

    //remove elements of the table if exist a display form
    reshapeEntity: function (responseEntity, formDisplay) {
        var self = this;
        var newResponseEntity = {};
        newResponseEntity.entity = responseEntity.entity;
        newResponseEntity.page = responseEntity.page;
        newResponseEntity.records = responseEntity.records;
        newResponseEntity.total = responseEntity.total;
        newResponseEntity.header = [];
        newResponseEntity.row = [];
        var indexElementsAdd = [];
        $.each(formDisplay.form.elements, function (kF, vF) {
            self.mergeFormAndTable(vF, newResponseEntity, indexElementsAdd, responseEntity);
        });
        if(self._referrer == "stakeholder"){
            indexElementsAdd.push(responseEntity.header.length - 2);
            newResponseEntity.header.push(responseEntity.header[responseEntity.header.length - 2]);            
        }
        indexElementsAdd.push(responseEntity.header.length - 1);
        var desabledElement = responseEntity.header.slice(-1).pop();
        newResponseEntity.header.push(desabledElement);
        //validate if first element responseEntity exist in newResponseEntity else add becouse is subrrogate key
        if (indexElementsAdd.indexOf(0) == -1) {
            responseEntity.header[0].dataType = -11;
            var firstElement = responseEntity.header[0];
            newResponseEntity.header.unshift(firstElement);
            indexElementsAdd.unshift(0);
        }

        var indexAdd = 0;
        $.each(responseEntity.row, function (k, v) {
            var newDataRow = [];
            for (var i = 0; i < indexElementsAdd.length; i++) {
                indexAdd = indexElementsAdd[i];
                newDataRow[i] = v[indexAdd];
            }
            newResponseEntity.row[k] = newDataRow;
        });
        return newResponseEntity;
    },

    renderTable: function (element, newResponseEntity, indexElementsAdd, responseEntity) {
        var self = this;
        if (element.container) {
            for (var i = 0; i < element.container.elements.length; i++) {
                self.renderTable(element.container.elements[i], newResponseEntity, indexElementsAdd, responseEntity);
            }
        }
        else {
            $.each(responseEntity.header, function (index, header) {
                if (element.render.properties.xpath) {
                    if (element.render.properties.xpath.toUpperCase() == header.fieldValue.toUpperCase()) {
                        //newResponseEntity.header.push(vE);

                        newResponseEntity.header.push($.extend(header, element.render.properties));
                        indexElementsAdd.push(index);
                    }
                }
            });
        }
    },

    mergeFormAndTable: function (element, newResponseEntity, indexElementsAdd, responseEntity) {
        var self = this;
        if (element.container) {
            for (var i = 0; i < element.container.elements.length; i++) {
                self.renderTable(element.container.elements[i], newResponseEntity, indexElementsAdd, responseEntity);
            }
        }
        else {
            $.each(responseEntity.header, function (index, header) {
                if (element.render.properties.xpath) {
                    if (element.render.properties.xpath.toUpperCase() == header.fieldValue.toUpperCase()) {
                        //newResponseEntity.header.push(header);
                        newResponseEntity.header.push($.extend(header, element.render.properties));
                        indexElementsAdd.push(index);
                    }
                }
            });
        }
    },

    /**
     * Return currentPageGrid
     * @returns {*}
     */
    getCurrentPageToSimpleGrid: function () {
        var self = this;
        return self._state.currentPageGrid;
    },

    /**
     * Set index page,
     * Initial value = 1. See simpleGrid widget for more details.
     * @param newIndex
     */
    setCurrentPageToSimpleGrid: function (newIndex) {
        var self = this;
        self._state.currentPageGrid = newIndex;
    },

    /**
     * Set initial state of widget
     */
    setInitialState: function () {
        var self = this;
        self.setCurrentPageToSimpleGrid("1");
    },

    /**
     * validate if the entity has a last report for show download last report link
     */
    existsLastReport: function () {
        var self = this;
        var defer = $.Deferred();
        var guidEntity = self.getSelectedItem().guidEntity;
        var $link = $(".admin-entity-last-report-link", self.content);
        $link.hide();
        $.when(self.dataService.getAdminEntitiesExistsLastReport(guidEntity))
            .done(function (response) {
                if (response.exists) $link.show();
                defer.resolve();
            });
        return defer.promise();
    },

    /**
     * check each x seconds for a specific entity for review the state and show a message
     * @param {string} guidEntityLast if this entities is send is because the method has been recalled but if the user has change the guidEntity selected this method stops
     */
    reviewUploadFileState: function (guidEntityLast) {
        var self = this;
        var guidEntity = self.getSelectedItem().guidEntity;
        if (guidEntityLast != undefined && guidEntityLast != guidEntity)
            return false;
        if (self.entitiesState[guidEntity] === undefined) {
            self.entitiesState[guidEntity] = true;//default value
        }
        self.showStateUploadingFile(self.entitiesState[guidEntity]);
        if (self.entitiesState[guidEntity] !== "UPLOADING") {
            $.when(self.dataService.getAdminEntityState(guidEntity))
                .done(function (data) {
                    if(self.entitiesState[guidEntity] == "InProgress" && data.state == "Finished"){
                        self.reloadEntitiesListAfterUpload(guidEntity);
                        self.showMessage(self.getResource("workportal-widget-admin-entities-upload-successful")+".", false);
                    }
                    self.entitiesState[guidEntity] = data.state;
                    if (self.showStateUploadingFile(data.state)) {
                        setTimeout(function () {
                            self.reviewUploadFileState(guidEntity);
                        }, 3000);
                    }
                    self.existsLastReport();
                });
        }
    },

    /**
     * reload the Entities list if has finish upload entities file
     * @param {*} guidEntity 
     */
    reloadEntitiesListAfterUpload: function(guidEntity) {
        var self=this;
        var guidEntityActual = self.getSelectedItem().guidEntity;
        if(guidEntityActual==guidEntity && self.actualShowingList){
            self.drawSimpleGrid({
                page: 1
            });
        }
    },

    /**
     * show the correct state message for entity and return true if is necessary to check the state again
     * @param {string} state (Error|InProgress|UPLOADING|*)
     */
    showStateUploadingFile: function (state) {
        var self = this;
        self.initialViewUploadEntities();
        self.enableUploadButton()
        var reCall = true;
        switch (state) {
            case "Error":
                var message = self.getResource("workportal-widget-admin-entities-upload-error");
                self.showMessage(message, true);
                break;
            case "InProgress":
                $("#entities-message-inProgress", self.content).css("display", "block");
                self.disableUploadButton();
                self.resizeAdminEntityWrapper();
                break;
            case "UPLOADING":
                $("#entities-message-uploading", self.content).css("display", "block");
                self.disableUploadButton();
                self.resizeAdminEntityWrapper();
                break;
            default:
                reCall = false;
                break;
        }
        return reCall;
    },

    /**
     * set hide to messages and enable upload button
     */
    initialViewUploadEntities: function () {
        var self=this;
        $("#uploadButton", self.content).attr("disabled", false);
        $("#entities-message-inProgress", self.content).hide();
        $("#entities-message-error", self.content).hide();
        $("#entities-message-uploading", self.content).hide();
        self.resizeAdminEntityWrapper();
    },

    /**
     * set disable upload button
     */
    disableUploadButton: function () {
        $("#uploadButton", self.content).addClass("disabled");
        $("#uploadButton", self.content).attr("for", "");
        $("#uploadButton", self.content).css("cursor", "not-allowed");
    },
    /**
     * set enable upload button
     */
    enableUploadButton: function () {
        $("#uploadButton", self.content).removeClass("disabled");
        $("#uploadButton", self.content).attr("for", "file-upload");
        $("#uploadButton", self.content).css("cursor", "pointer");
    },

    /**
     * download last entity report link
     */
    downloadLastReport: function () {
        var self = this;
        var guidEntity = self.getSelectedItem().guidEntity;
        self.dataService.getAdminEntitiesDownloadLastReport(guidEntity);
    }

});
