/**
*  Name: BizAgi Case Security Improvement
* 
* @author Nessler Camargo
*/


bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.transfer.case.security.permissions", {}, {

    init: function (workportalFacade, dataService, params) {
        var self = this;
        self.selectedUsers = ["", ""];
        self.allProcess = false;
        self.haveProcess = false;

        // Call base
        self._super(workportalFacade, dataService, params);

        //Load templates
        self.loadTemplates({
            "admin-case-security-improvement": bizagi.getTemplate("bizagi.workportal.widgets.admin.transfer.case.security.permissions").concat("#ui-bizagi-workportal-widget-admin-case-security-improvement"),
            "admin-case-security-improvement-fields": bizagi.getTemplate("bizagi.workportal.widgets.admin.transfer.case.security.permissions").concat("#ui-bizagi-workportal-widget-admin-case-security-improvement-fields"),
            "security-improvement-loader": bizagi.getTemplate("bizagi.workportal.widgets.admin.transfer.case.security.permissions").concat("#ui-bizagi-workportal-widget-admin-case-security-improvement-loader"),
            "security-improvement-emptyTree": bizagi.getTemplate("bizagi.workportal.widgets.admin.transfer.case.security.permissions").concat("#ui-bizagi-workportal-widget-admin-case-security-improvement-emptyTree"),
            useNewEngine: false
        });
    },

    getWidgetName: function () {
        return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_TRANSFER_CASE_SECURITY_PERMISSIONS;
    },

    loadtemplates: function () {
        var self = this;
        //Template vars 
        self.fieldsContent = self.getTemplate("admin-case-security-improvement-fields");
        self.loaderTmpl = self.getTemplate("security-improvement-loader");
        self.emptyTreeTmpl = self.getTemplate("security-improvement-emptyTree");
    },

    postRender: function () {
        var self = this;
        self.setupData();
    },

    /*
    * Setup form data
    */
    setupData: function () {
        var self = this;

        self.setupInitialData();
    },

    /*
    * Load form data
    */
    setupInitialData: function () {
        var self = this;
        //append fields to wrapper
        var fields = $.tmpl(self.fieldsContent, {});
        $("#defaults-admin-case-security-home", self.content).append(fields);
        self.setupSearchusersButton();
        $(".security-message-danger", self.content).hide();
        $("#title-process-revoke", self.content).hide();
        var tree = $.tmpl(self.emptyTreeTmpl, { emptyTreeText: bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-select-current-user") });
        $("#processTreePermissions", self.content).addClass("disableColor").append(tree);
    },

    renderContent: function () {
        var self = this;
        self.generalContent = self.getTemplate("admin-case-security-improvement");
        var content;
        content = self.content = $.tmpl(self.generalContent, {});
        if (self.params.content) {
            content = self.params.content.html(content);
        }
        self.loadtemplates();
        return content;
    },


    setupProcessTreePermissions: function (id) {
        var self = this,
            content = self.getContent();

        var processtreeParams = $.extend({}, self.params);
        processtreeParams.canvas = $("#processTreePermissions", content);
        processtreeParams.svgIconsMode = true;
        processtreeParams.searchKeyResource = " ";
        processtreeParams.byUser = true;
        processtreeParams.userId = id;
        processtreeParams.showTitle = false;
        processtreeParams.newDesign = true;
        $("#processTreePermissions", content).removeClass("disableColor");
        //tree for delete cases
        self.processTreePermissions = new bizagi.workportal.widgets.processtree(self.workportalFacade, self.dataService, processtreeParams);
        self.processTreePermissions.render();
    },

    /*
    * Call service 
    */

    /*
    * initialize the users search form widget
    */
    setupUsersSearchForm: function (input, user) {
        var self = this;
        var content = self.getContent();
        var def = $.Deferred();
        $("#defaults-admin-case-security-home").hide();

        self.usersTable = new bizagi.workportal.widgets.userstable(self.workportalFacade, self.dataService, $.extend(self.params, {
            canvas: $("#dinamicContent", content),
            userLinkLabel: [bizagi.localization.getResource("workportal-widget-admin-defaults-assignation-select-link")],
            parentDef: def,
            showBackButton: true
        }));

        self.usersTable.render();

        $.when(def).done(function (userId, userName) {
            var params = { 
                action: "Applications", 
                userId: userId 
            }
            if (user == 0) {
                $.when(self.dataService.getApplicationCategoriesList(params))
                    .done(function (applicationList) {
                        self.endLoading();
                        if (applicationList.items.length > 0) {
                            self.allProcess = true;
                            self.haveProcess = true;
                            var typeOption = $("div.big-input > input[type='radio']:checked").val(),
                                text = bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-transfer-permissions-of");
                            if (typeOption == "revoke") {
                                text = bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-revoke-permissions-of");
                            }
                            self.setupProcessTreePermissions(userId, text)
                        } else {
                            self.initializeTree(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-user-dont-have-permissions-over-processes"));
                            self.allProcess = false;
                            self.haveProcess = false;
                        }

                    }).fail(function () {
                        self.endLoading();
                        self.initializeTree(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-user-dont-have-permissions-over-processes"));
                    });
            }
            $("#dinamicContent", content).html("");
            $("#defaults-admin-case-security-home", content).show();
            if (userName) {
                $(input, content).val(userName);
                self.showHideClearIcon(input, 'block')
            }
            if (userId) {
                self.selectedUsers[user] = userId;
            }
        });
    },

    initializeTree: function (text) {
        var tree = $.tmpl(self.emptyTreeTmpl, { emptyTreeText: text });
        $("#processTreePermissions", self.content).addClass("disableColor").empty();
        $("#processTreePermissions", self.content).append(tree);
    },

    showHideClearIcon: function (id, display) {
        self = this;
        var content = self.getContent();
        $(id + " ~ span", content).css("display", display);
    },

    transferCaseSecuritypermissions: function () {
        var self = this;
        self.loader = $.tmpl(self.loaderTmpl, {});
        var params = {};
        if (self.haveProcess) {
            params = self.paramsToInvalidateSelectedCases();
        }

        params.allProcess = self.allProcess;
        params.idUserSource = (self.selectedUsers[0] != "") ? self.selectedUsers[0] : -1;
        params.idUserTarget = (self.selectedUsers[1] != "") ? self.selectedUsers[1] : -1;

        if (params.idUserSource == -1 || params.idUserTarget == -1) {
            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-fail-you-must-select-both-users-to-continue"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
        }
        else if (params.idUserSource == params.idUserTarget) {
            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-fail-users-must-be-different"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
        }
        else if (!self.haveProcess) {
            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-user-dont-have-permissions-over-processes"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
        }
        else {
            var customOptions = {
                dialogClass: "widget-admincasesearch-invalidate-cases-confirm",
                width: 400,
                minWidth: 400
            };
            $.when(bizagi.showConfirmationBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-confirmation-message"), bizagi.localization.getResource("workportal-general-word-information"), "warning", [
                { 
                    label: bizagi.localization.getResource("workportal-general-word-no"), 
                    action: "reject" 
                }, 
                { 
                    label: bizagi.localization.getResource("workportal-general-word-yes"), 
                    action: "resolve" 
                }
                ], customOptions))
                .done(function () {
                    self.showLoading();
                    $.when(self.dataService.transferCaseSecuritypermissions(params))
                        .done(function (response) {
                            self.endLoading();
                            if (response.status === "true" || response.status === true) {
                                self.showNotifier(printf(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-ok"), response.quantityModifiedCases));
                            }
                            else {
                                self.endLoading();
                                bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
                            }
                        })
                        .fail(function (error) {
                            self.endLoading();
                            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
                            bizagi.log(error);
                        });
                });

        }

    },

    revokeCaseSecuritypermissions: function () {
        var self = this;
        self.loader = $.tmpl(self.loaderTmpl, {});
        var params = {};
        if (self.haveProcess) {
            params = self.paramsToInvalidateSelectedCases();
        }
        params.allProcess = self.allProcess;
        params.idUserSource = (self.selectedUsers[0] != "") ? self.selectedUsers[0] : -1;

        if (params.idUserSource == -1 || params.idUserTarget == -1) {
            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-fail-you-must-select-the-current-user-to-continue"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
        }
        else if (!self.haveProcess) {
            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-user-dont-have-permissions-over-processes"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
        }
        else {
            var customOptions = {
                dialogClass: "widget-admincasesearch-invalidate-cases-confirm",
                width: 400,
                minWidth: 400
            };
            $.when(bizagi.showConfirmationBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-confirmation-message"), bizagi.localization.getResource("workportal-general-word-information"), "warning", [
                { 
                    label: bizagi.localization.getResource("workportal-general-word-no"), 
                    action: "reject" 
                }, 
                { 
                    label: bizagi.localization.getResource("workportal-general-word-yes"), 
                    action: "resolve" 
                }
            ], customOptions))
                .done(function () {
                    self.showLoading();
                    $.when(self.dataService.revokeCaseSecuritypermissions(params))
                        .done(function (response) {

                            self.endLoading();
                            if (response.status === "true" || response.status === true) {
                                self.showNotifier(printf(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-revoke-ok"), response.quantityModifiedCases));
                            }
                            else {
                                bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
                            }
                        })
                        .fail(function (error) {
                            self.endLoading();
                            bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-assign-fail"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
                            bizagi.log(error);
                        });
                })
                .fail(function (error) {
                    console.log("error", error);
                });
        }

    },

    paramsToInvalidateSelectedCases: function () {
        var self = this;
        var selectedProcessRoute = self.processTreePermissions.getTreeRouteSelected();
        var idAppToSend = "",
            idProcessToSend = "";

        if (selectedProcessRoute.length > 0) {
            if (selectedProcessRoute.length == 2) {
                idAppToSend = selectedProcessRoute[1];
                idProcessToSend = selectedProcessRoute[0];
            }
            else {
                idAppToSend = selectedProcessRoute[selectedProcessRoute.length - 1];
                idProcessToSend = selectedProcessRoute[0];
            }
        }
        if (idProcessToSend == "") {
            self.allProcess = true;
        }
        else {
            self.allProcess = false;
        }

        var params = {
            idProcess: idProcessToSend,
            idApplication: idAppToSend
        };
        return params;
    },

    showNotifier: function (message) {
        var notifier = window.parent.bizagi.injector.get('notifier');
        notifier.showSucessMessage(message, '');
    },
    showLoading: function () {
        var self = this;
        if(bizagi.util.isIE()){
           $("#defaults-admin-case-security-home").focus();
        }
        $("#defaults-admin-case-security-home").append(self.loader);
    },
    endLoading: function () {
        var self = this;
        $(self.loader).remove();
    },
    clearInput: function (input, content) {
        var self = this;
        var position = (input == "#txtTextUserSource") ? 0 : 1;
        self.showHideClearIcon(input, 'none');
        $(input, content).val("");
        self.selectedUsers[position] = "";
    },
    /****************** EVENTOS ***********************/


    setupSearchusersButton: function () {
        var self = this,
            content = self.getContent();

        $("#btn-searchUserSource", content).click(function () {
            self.showLoading();
            self.setupUsersSearchForm("#txtTextUserSource", 0);
        });
        $("#btn-searchUserTarget", content).click(function () {
            self.setupUsersSearchForm("#txtTextUserTarget", 1);
        });

        $("#btn-clearUserSource", content).click(function () {
            self.clearInput("#txtTextUserSource", content);
            var tree = $.tmpl(self.emptyTreeTmpl, { emptyTreeText: bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-select-current-user") });
            $("#processTreePermissions", self.content).addClass("disableColor").empty();
            $("#processTreePermissions", self.content).append(tree);
        });
        $("#btn-clearUserTarget", content).click(function () {
            self.clearInput("#txtTextUserTarget", content);
        });

        $("#btn-copyPermissions", content).click(function () {
            if($("#allAppsLinkSelected", content).data('allAppSelected') === false && self.processTreePermissions.getTreeRouteSelected().length === 0){ //If nothing has been selected
                bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-processTree-no-selected-process"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
            }else{
                self.transferCaseSecuritypermissions();
            }
        });
        $("#btn-revokePermissions", content).click(function () {
            if($("#allAppsLinkSelected", content).data('allAppSelected') === false && self.processTreePermissions.getTreeRouteSelected().length === 0){ //If nothing has been selected
                bizagi.showMessageBox(bizagi.localization.getResource("workportal-widget-processTree-no-selected-process"), bizagi.localization.getResource("workportal-general-word-information"), "warning");
            }else{
                self.revokeCaseSecuritypermissions();
            }
        });

        $("#transferPermissions", content).click(function () {
            $("input[value='copy']").attr('checked', true);
            $(".security-message-information", content).show();
            $(".security-message-danger", content).hide();
            $("#btn-copyPermissions", content).show();
            $("#btn-revokePermissions", content).hide();
            $(".user-label:has(#txtTextUserTarget)").show();
            $("#title-process-copy", content).show();
            $("#title-process-revoke", content).hide();
            $("#revokePermissions", content).removeClass("ui-rubik-button-accept").addClass("ui-rubik-button-cancel");
            $("#transferPermissions", content).removeClass("ui-rubik-button-cancel").addClass("ui-rubik-button-accept");
            self.clearInput("#txtTextUserSource", content);
            self.clearInput("#txtTextUserTarget", content);
            var tree = $.tmpl(self.emptyTreeTmpl, { emptyTreeText: bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-select-current-user") });
            $("#processTreePermissions", self.content).addClass("disableColor").empty();
            $("#processTreePermissions", self.content).append(tree);
        })
        $("#revokePermissions", content).click(function () {
            $("input[value='revoke']").attr('checked', true);
            $(".security-message-information", content).hide();
            $(".security-message-danger", content).show();
            $("#btn-copyPermissions", content).hide();
            $("#btn-revokePermissions", content).show();
            $(".user-label:has(#txtTextUserTarget)").hide();
            $("#title-process-copy", content).hide();
            $("#title-process-revoke", content).show();
            $("#transferPermissions", content).removeClass("ui-rubik-button-accept").addClass("ui-rubik-button-cancel");
            $("#revokePermissions", content).removeClass("ui-rubik-button-cancel").addClass("ui-rubik-button-accept");
            self.clearInput("#txtTextUserSource", content);
            self.clearInput("#txtTextUserTarget", content);
            var tree = $.tmpl(self.emptyTreeTmpl, { emptyTreeText: bizagi.localization.getResource("workportal-widget-admin-transfers-case-security-permissions-select-current-user") });
            $("#processTreePermissions", self.content).addClass("disableColor").empty();
            $("#processTreePermissions", self.content).append(tree);
        })
    }

});


