
/**
 *   Name: BizAgi Workportal Users Delegation
 *   Author: Ivan Ricardo Taimal
 *   Comments:
 *   -   This script renders the users delegate per process
 */
bizagi.workportal.widgets.admin.usersDelegation.extend("bizagi.workportal.widgets.admin.usersDelegation", {}, {

    /**
     * To be overriden in each device to apply layouts
     */
    postRender: function () {
        var self = this;
        self.maxPages = 10;
        self.maxRows = 10;
        self.currentPage = 1;
        self.showTable = true;
        self.orderType = "ASC";
        self.orderField = "";
        self.dateFormat = self.getResource("dateFormat") + " " + self.getResource("timeFormat");
        self.$delegationFormWrapper = $("#widget-userdelegation__form-wrapper", self.content);
        self.$DelegationListWrapper = $("#widget-userdelegation__configurations-wrapper", self.content);

        self.tableHeaders = [
            {
                "displayName": self.getResource("workportal-widget-userdelegation-process"),
                "fieldValue": "processName"
            },
            {
                "displayName": self.getResource("workportal-widget-userdelegation-username"),
                "fieldValue": "userName"
            },
            {
                "displayName": self.getResource("workportal-widget-userdelegation-delegatedname"),
                "fieldValue": "delegatedName"
            },
            {
                "displayName": self.getResource("workportal-widget-userdelegation-init-date"),
                "fieldValue": "startDate"
            },
            {
                "displayName": self.getResource("workportal-widget-userdelegation-end-date"),
                "fieldValue": "endDate"
            }
        ];
        
        self.renderDelegationForm(); // >> 1
        self.initDelegationTable(); //  >> 3
        self.addEventsHandler(); // >> 4
        
    },

    /** >> 1
     * renderDelegationForm
     */
    renderDelegationForm: function () {
        var self = this;
        var userdelegationFormTmpl = self.getTemplate("admin.userdelegationFormTmpl");

        $.tmpl(userdelegationFormTmpl, { currentUser: "TestUser" }).appendTo(self.$delegationFormWrapper);

        var startDateWrapper = $("#widget-userdelegation__start-date", self.content);
        var endDateWrapper = $("#widget-userdelegation__end-date", self.content);
        var today = new Date();
        startDateWrapper.datepicker({ minDate: today });
        endDateWrapper.datepicker({ minDate: today });
        self.configureProcessCombo();

        $("#widget-userdelegation__start-date-btn", self.content).click(function () {
            startDateWrapper.datepicker("show");
        });
        $("#widget-userdelegation__end-date-btn", self.content).click(function () {
            endDateWrapper.datepicker("show");
        });

    },

    // >> 1.1
    configureProcessCombo: function () {
        var self = this;
        var params = {
            idUser: bizagi.currentUser.idUser
        };
        $.when(self.dataService.getProcessToDelegate(params)).done(function (response) {
            var processComboWrapper = $(".widget-userdelegation__delegated-process-combo-wrapper", self.content);
            var comboDataSource = { combo: [], id: "widget-userdelegation__process-to-delegate-combo" };
            comboDataSource.combo = comboDataSource.combo.concat(response.process);
            processComboWrapper.empty();
            self.processComponent = self.setupMultiselector(processComboWrapper, response.process);
        });
    },

    /** >> 1.1.1
     * Multiselector
     */

    setupMultiselector: function (element, options) {
        var self = this;
        var component = {
            options: options,
            element: element,
            values: []
        };

        element.uicombo({
            data: { combo: options },
            isEditable: true,
            isSearchable: true,
            initValue: "tetetet",
            itemValue: function (item) {
                return item.id;
            },
            itemText: function (item) {
                return item.displayName;
            },
            onComplete: function () {
                var msgPlaceholder = bizagi.localization.getResource("workportal-widget-userdelegation-please-select-your-process");
                $(".biz-wp-combo-input", element).addClass("widget-user-delegation__multiselector-input");
                $(".biz-wp-combo-input").attr("placeholder", msgPlaceholder);
                element.prepend('<div class="widget-user-delegation__multiselector"></div>');
            },
            onComboDropDown: function () {
                self.markChoicesMultiselector(component);
            },
            onChange: function (obj) {
                var inputText = obj.ui.val() || "";
                var value = obj.ui.data("value");
                self.filterOptionsMultiselector(component, inputText);
                self.addChoiceMultiselector(component, value, true);
            }
        });

        return component;
    },

    // >> 1.1.1.1
    markChoicesMultiselector: function (component) {
        $("li", component.element).removeClass("widget-user-delegation__multiselector__option--selected");
        $("li", component.element).prepend('<i class="widget-user-delegation__multiselector__option__icon"></i>');
        $.each(component.values, function (index, value) {
            $('li[data-value="' + value + '"]', component.element).addClass("widget-user-delegation__multiselector__option--selected");
        });
    },

    // >> 1.1.1.2
    filterOptionsMultiselector: function (component, inputText) {
        var matches = component.options.filter(function (item) {
            return item.displayName.toLowerCase().indexOf(inputText.toLowerCase()) == 0;
        });
        $("li", component.element).hide();
        $.each(matches, function (index, item) {
            $('li[data-value="' + item.id + '"]', component.element).show();
        });
    },

    // >> 1.1.1.3
    addChoiceMultiselector: function (component, value, editable) {
        if (value) {
            // var selectedOption = component.options.filter(function (item) {
            var selectedOption = component.options.find(function (item) {
                return item.id == value;
            // })[0];
            });
            var displayName = selectedOption.displayName;
            if (component.values.indexOf(value + "") === -1) {
                var itemBody = '<div class="widget-user-delegation__multiselector__item" data-value =' + value + ">" + displayName + ' | <i class="widget-user-delegation__multiselector__item-clear-btn bz-icon bz-icon-close"></i></div>';
                $(".widget-user-delegation__multiselector", component.element).append(itemBody);
                $(".biz-wp-combo-input", component.element).css({ "box-shadow": "none" });
                $(".widget-user-delegation__multiselector-input", component.element).val("");
                component.values.push(value + "");

                if (editable) {
                    $(".widget-user-delegation__multiselector__item-clear-btn", component.element).click(function () {
                        var itemToDelete = $(this).parent();
                        var indexToRemove = component.values.indexOf((itemToDelete.data()).value + "");
                        if (indexToRemove >= 0) {
                            component.values.splice(indexToRemove, 1);
                            itemToDelete.remove();
                            if (component.values.length && component.values.length < 1) {
                                $(".biz-wp-combo", component.element).show();
                            }
                        }
                    });
                }
            }
        }
    },

    /** >> 3
     * Query Delegations
     */
    initDelegationTable: function (filterData) {
        var self = this;
        var params = filterData || {
            pag: 1,
            pagSize: self.maxRows
        };
        params.idUser = bizagi.currentUser.idUser;

        var userdelegationListTmpl = self.getTemplate("admin.userdelegationListTmpl");
        
        self.$DelegationListWrapper.html("");
        $.when(self.dataService.queryDelegations(params)).done(function (response) {
            //render delegations table widget
            if (response.delegations.length > 0) {

                var pagination = (response.records > 1) ? true : false;
                var totalPages = Math.ceil(response.records / self.maxRows);
                var pagesArray = self.getPagesArray(totalPages);

                $.tmpl(userdelegationListTmpl, {
                    headers: self.tableHeaders,
                    orderField: self.orderField,
                    orderType: self.orderType,
                    delegationList: response.delegations,
                    page: response.page,
                    pagination: pagination,
                    pagesArray: pagesArray
                }).appendTo(self.$DelegationListWrapper);
                
                self.setUpPagination(totalPages);
                self.setUpSortColumnLinks();
                $(".widget-userdelegation__separator__btn-icon", self.content).addClass("widget-userdelegation__separator__btn-icon--opened");
            }
            else {
                var userdelegationEmptyListTmpl = self.getTemplate("admin.userdelegationEmptyMessageTmpl");
                var msg = bizagi.localization.getResource("workportal-widget-userdelegation-empty-list-message");
                self.$DelegationListWrapper.html($.tmpl(userdelegationEmptyListTmpl, { message: msg }));
            }

            self.addTableEventsHandler();
            self.delegatedAssignment = {
                element: $("#widget-userdelegation__delegated-assignment-user", self.content),
                value: ""
            };
        });
    },

    /* >> 3.1
     * Get Pages
     */
    getPagesArray: function (totalPages) {
        var self = this;
        var pagesToShow = (self.maxPages > totalPages) ? totalPages : self.maxPages;
        var aux = [];

        for (var a = 0; a < pagesToShow; a++) {
            aux.push(a + 1);
        }
        return aux;
    },

    /* >> 3.2
     * Set pagination
     */
    setUpPagination: function (totalPages) {
        var self = this;
        var $pager = $("#widget-userdelegation__delegation-list__table-pager ul", self.content);

        $pager.bizagiPagination({
            maxElemShow: self.maxRows,
            totalPages: totalPages,
            actualPage: self.currentPage,
            listElement: $pager,
            clickCallBack: function (options) {
                var filterData = {
                    pag: self.currentPage = parseInt(options.page),
                    pagSize: self.maxRows,
                    orderField: self.orderField,
                    orderType: self.orderType
                };
                self.initDelegationTable(filterData);
            }
        });

    },

    /* >> 3.3
     * assign click event to each column to order
     */
    setUpSortColumnLinks: function () {
        var self = this;
        $(".biz-wp-table-head th", self.content).click(function () {
            self.orderField = $(this).data("orderfield");
            self.orderType = $(this).data("ordertype") || self.orderType;
            var filterData = {
                orderType: self.orderType,
                orderField: self.orderField,
                pagSize: self.maxRows,
                pag: self.currentPage
            };
            self.initDelegationTable(filterData);
        });
    },

    // >> 3.4
    addTableEventsHandler: function () {
        var self = this;
        var content = self.getContent();
        //table edit-btn
        $(".widget-userdelegation__delegation-list__edit-btn", self.content).click(function () {
            self.selectedDelegation = $(this).parent().data();
            self.delegatedAssignment.element.val(self.selectedDelegation.delegatedName);
            self.delegatedAssignment.value = self.selectedDelegation.idDelegate;
            $(".biz-wp-combo", self.processComponent.element).hide();
            self.clearMultiselector(self.processComponent);

            self.addChoiceMultiselector(self.processComponent, self.selectedDelegation.idProcess, false);
            $(".widget-userdelegation__delegated-assignment--edition", self.content).show();
            $(".widget-userdelegation__delegated-assignment", self.content).hide();
            $(".widget-userdelegation__delegated-assignment--edition__value", self.content).text(self.selectedDelegation.delegatedName);
            $("#widget-userdelegation__start-date", self.content).datepicker("setDate", new Date(Date.parse(self.selectedDelegation.startDate, "MM/DD/YYYY")));
            $("#widget-userdelegation__end-date", self.content).datepicker("setDate", new Date(Date.parse(self.selectedDelegation.endDate, "MM/DD/YYYY")));
            $(".widget-user-delegation__multiselector__item-clear-btn", self.content).hide();

            $("#widget-userdelegation__delegated-startdate--edition", self.content).show();
            $(".widget-userdelegation__delegated-startdate--edition__value", self.content).text(self.selectedDelegation.startDate);
            $("#widget-userdelegation__form-input_date_startdate", self.content).hide();
        });

        $(".widget-userdelegation__delegation-list__delete-btn", self.content).click(function () {
            var selectedDelegation = $(this).parent().data();
            self.openDeleteConfirmation(selectedDelegation);
        });

        //separator button event
        $(".widget-userdelegation__separator__btn", content).click(function () {
            var delegationList = $(".widget-userdelegation__delegation-list", content);
            self.showTable = !self.showTable;
            if (self.showTable) {
                $(".widget-userdelegation__separator__btn-icon", content).addClass("widget-userdelegation__separator__btn-icon--opened");
                delegationList.show();
            } else {
                $(".widget-userdelegation__separator__btn-icon", content).removeClass("widget-userdelegation__separator__btn-icon--opened");
                delegationList.hide();
            }
        });
    },

    // >> 3.4.1
    openDeleteConfirmation: function (selectedDelegation) {
        var self = this;
        var deleteConfirmationDialog = $.tmpl(self.getTemplate("admin.userdelegationDeleteConfirmationTmpl"), { delegation: selectedDelegation });
        deleteConfirmationDialog.dialog({
            resizable: false,
            draggable: false,
            height: "auto",
            width: "600px",
            modal: true,
            title: selectedDelegation.processName,
            maximize: false,
            close: function () {
                deleteConfirmationDialog.dialog("destroy");
                deleteConfirmationDialog.detach();
            },
            buttons: [
                {
                    text: self.getResource("workportal-general-button-label-ok"),
                    click: function () {
                        $(this).dialog("destroy");
                        self.deleteDelegation(selectedDelegation);
                    }
                },
                {
                    text: self.getResource("workportal-widget-queries-confirm-cancel"),
                    click: function () {
                        $(this).dialog("destroy");
                    }
                }
            ]
        });
    },

    /** >> 3.4.1.1
     * Delete configured delegation
     */
    deleteDelegation: function (params) {
        var self = this;
        $.when(self.dataService.deleteDelegation(params)).done(function (response) {
            // console.log(response);//Todo: catch errors
            self.initDelegationTable();
        });
    },

    /** >> 4
     * Events Handlers
     */
    addEventsHandler: function () {
        var self = this;
        var content = self.getContent();
        //bread crumb 1
        $(".widget-userdelegation__bread-crumbs__link-1", self.content).click(function () {
            var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-1");
            $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);
            $(".widget-userdelegation__wrapper", content).show();
            $(".widget-userdelegation__bread-crumbs", content).hide();
            $("#widget-userdelegation__getuser-wrapper", content).html("");
            $("#widget-userdelegation__getuser-wrapper", content).hide();
        });

        //bread crumb 2
        $(".widget-userdelegation__bread-crumbs__link-2", self.content).click(function () {
            var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-2");
            $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);
            $(".widget-userdelegation__wrapper", content).hide();
            $(".widget-userdelegation__bread-crumbs", content).show();
            $(".widget-userdelegation__bread-crumbs__link-3", self.content).hide();
            $("#widget-userdelegation__getuser-wrapper", content).html("");
            self.setupUsersSearchForm(self.delegatedAssignment);
        });

        //Delegated assignment
        $("#widget-userdelegation__delegated-assignment-user", content).focus(function () {
            self.setupUsersSearchForm(self.delegatedAssignment);
        });
        $("#widget-userdelegation__delegated-assignment-search-btn", content).click(function () {
            var msg = bizagi.localization.getResource("workportal-widget-userdelegation-bread-crumb-2");
            $(".ui-widget-header.ui-corner-all > span.ui-dialog-title").html(msg);
            self.setupUsersSearchForm(self.delegatedAssignment);
        });
        $("#widget-userdelegation__delegated-assignment-clear-btn", content).click(function () {
            $("#widget-userdelegation__delegated-assignment-user", content).val("");
            self.delegatedAssignment.value = '';
        });

        $("#widget-userdelegation__new-assignment-save-btn", content).click(function () {
            if (self.selectedDelegation) {
                self.updateDelegation();
            } else {
                self.saveDelegation();
            }
        });

        $("#widget-userdelegation__new-assignment-clear-btn", content).click(function () {
            self.clearForm();
        });
    },

    /** >> 4.1
     * initialize the users search form widget
     */
    setupUsersSearchForm: function (params) {
        var self = this;
        var content = self.getContent();
        var def = $.Deferred();
        $(".widget-userdelegation__bread-crumbs", content).show();
        $(".widget-userdelegation__wrapper", content).hide();

        $("#widget-userdelegation__getuser-wrapper", content).show();
        self.usersTable = new bizagi.workportal.widgets.admin.usersDelegation.searchUser(self.workportalFacade, self.dataService, $.extend(self.params, {
            canvas: $("#widget-userdelegation__getuser-wrapper", content),
            userLinkLabel: [bizagi.localization.getResource("workportal-widget-admin-defaults-assignation-select-link")],
            parentDef: def
        }));

        self.usersTable.render();

        $.when(def).done(function (userId, userName) {
            $("#widget-userdelegation__getuser-wrapper", content).html("");
            $("#widget-userdelegation__getuser-wrapper", content).hide();
            params.element.val(userName);
            params.value = userId;
            $(".widget-userdelegation__wrapper", content).show();
            $(".widget-userdelegation__bread-crumbs", content).hide();
            $(".widget-userdelegation__bread-crumbs li:last-child", content).hide();

        });
    },

    /** >> 4.2
     * Update configured delegation
     */
    updateDelegation: function () {
        var self = this;
        var startDate = $("#widget-userdelegation__start-date", self.content).datepicker("getDate");
        var endDate = $("#widget-userdelegation__end-date", self.content).datepicker("getDate");
        var params = {
            idDelegation: self.selectedDelegation.idDelegation,
            idUser: bizagi.currentUser.idUser,
            idDelegate: self.delegatedAssignment.value,
            idsProcess: JSON.stringify(self.processComponent.values),
            startDate: bizagi.util.dateFormatter.formatDate(startDate, "yyyy-MM-dd"),
            endDate: bizagi.util.dateFormatter.formatDate(endDate, "yyyy-MM-dd"),
            enableSub: 1
        };
        if (self.validateSaveUpdateParams(params)) {
            $.when(self.dataService.updateDelegation(params)).done(function () {
                self.clearForm();
                self.initDelegationTable();
                self.selectedDelegation = null;
            }).fail(function (response) {
                var arrayResult = response.responseJSON.messages;
                var msgs = [];
                for (var i = 0; i < arrayResult.length ; i++) {
                    var arr = arrayResult[i][i].error;
                    var messages = arrayResult[i][i].message;
                    msgs.push(bizagi.localization.getResource(arr) + " <br /> <b>" + messages + "</b> <br /> ");
                }
                $("#widget-userdelegation__assignation--error", self.content).show();
                $("#widget-userdelegation__assignation--error_value", self.content).html(msgs.toString());
            });
        }
    },

    /** >> 4.3
     * Save configured delegation
     */
    saveDelegation: function () {
        var self = this;
        var startDate = $("#widget-userdelegation__start-date", self.content).datepicker("getDate");
        var endDate = $("#widget-userdelegation__end-date", self.content).datepicker("getDate");
        var params = {
            idUser: bizagi.currentUser.idUser,
            idDelegate: self.delegatedAssignment.value,
            idsProcess: JSON.stringify(self.processComponent.values),
            startDate: bizagi.util.dateFormatter.formatDate(startDate, "yyyy-MM-dd"),
            endDate: bizagi.util.dateFormatter.formatDate(endDate, "yyyy-MM-dd"),
            enableSub: 1
        };
        if (self.validateSaveUpdateParams(params)) {
            $.when(self.dataService.saveDelegation(params)).done(function () {
                self.clearForm();
                self.initDelegationTable();
                self.selectedDelegation = null;
            }).fail(function (response) {
                var arrayResult = response.responseJSON.messages;
                var msgs = [];
                for (var i = 0; i < arrayResult.length ; i++) {
                    var arr = arrayResult[i][i].error;
                    var messages = arrayResult[i][i].message;
                    msgs.push(bizagi.localization.getResource(arr) + " <br /> <b>" + messages + "</b> <br /> ");
                }
                $("#widget-userdelegation__assignation--error", self.content).show();
                $("#widget-userdelegation__assignation--error_value", self.content).html(msgs.toString());
            });
        }
    },

    // >> 4.3.1 - 4.2.1
    validateSaveUpdateParams: function (params) {
        var self = this;
        var content = self.getContent();
        var startDate = new Date(Date.parse(params.startDate, "MM/DD/YYYY HH:mm:ss"));
        var endDate = new Date(Date.parse(params.endDate, "MM/DD/YYYY HH:mm:ss"));
        $("#widget-userdelegation__delegated-process-combo-alert", content).hide();
        $("#widget-userdelegation__delegated-assignment-user-alert", content).hide();
        $("#widget-userdelegation__start-date-alert", content).hide();
        $("#widget-userdelegation__end-date-alert", content).hide();
        $("#widget-userdelegation__delegation-period-alert", content).hide();

        if (params.idsProcess == "[]") {
            $("#widget-userdelegation__delegated-process-combo-alert", content).show();
        }
        ;
        if (bizagi.util.isEmpty(params.idDelegate)) {
            $("#widget-userdelegation__delegated-assignment-user-alert", content).show();
        }
        if (!params.startDate) {
            $("#widget-userdelegation__start-date-alert", content).show();
        }
        if (!params.endDate) {
            $("#widget-userdelegation__end-date-alert", content).show();
        }
        if (endDate < startDate) {
            $("#widget-userdelegation__delegation-period-alert", content).show();
        }

        return params.idUser && params.idsProcess && !bizagi.util.isEmpty(params.idDelegate) && params.startDate && params.endDate && endDate >= startDate;
    },

    // >> 4.4
    clearForm: function () {
        var self = this;
        self.clearMultiselector(self.processComponent);
        self.delegatedAssignment.element.val("");
        $(".biz-wp-combo", self.processComponent.element).show();
        $(".workportal-widget-userdelegation__sub-delegation__checkbox").prop("checked", false);
        $("#widget-userdelegation__process-to-delegate-combo", self.content).val("");
        $("#widget-userdelegation__start-date", self.content).val("");
        $("#widget-userdelegation__end-date", self.content).val("");
        $("#widget-userdelegation__getuser-wrapper", self.content).html("");
        $(".widget-userdelegation__delegated-assignment--edition", self.content).hide();
        $(".widget-userdelegation__delegated-assignment", self.content).show();
        $("#widget-userdelegation__delegated-startdate--edition", self.content).hide();
        $(".widget-user-delegation__multiselector__item-clear-btn", self.content).show();
        $("#widget-userdelegation__form-input_date_startdate", self.content).show();
        $("#widget-userdelegation__assignation--error", self.content).hide();
    },

    // >> 3.4.2 - 4.4.1
    clearMultiselector: function (component) {
        component.element.val("");
        $(".widget-user-delegation__multiselector", component.element).html("");
        component.values = [];
    }

});
